import React from 'react';
import styles from './LineLogin.module.css'; // CSS

function LineLogin() {
  const loginEvent = () => {
    let URL = 'https://access.line.me/oauth2/v2.1/authorize?';
    URL += 'response_type=code'; // LINE code
    URL += `&client_id=${process.env.REACT_APP_LINE_LOGIN_ID}`; // ID
    URL += `&redirect_uri=${process.env.REACT_APP_LINE_CBURL}`; // 
    URL += '&state=123456789'; //   123456789
    URL += '&scope=openid%20profile'; // 
    URL += '&nonce=helloWorld'; // 
    URL += '&bot_prompt=normal'; // 
    window.open(URL, '_self'); // 
  };

  return (
    <div className={styles.container}>
      <h1>InstAi v0.7<br/>Line-Notify-Practice</h1>
      <button className={styles.loginButton} onClick={loginEvent}>
         Line
      </button>
    </div>
  );
}

export default LineLogin;
